.contacts .checkall {\n    color: #999999;\n    margin-left: 15px;\n    white-space: nowrap;\n}\n.contacts .mint-searchbar {\n    background-color: #eee;\n    padding: 0;\n    margin: 0 15px;\n    width: 100%;\n}\n.contacts .mint-searchbar-inner {\n    height: 40px;\n}\n.contacts .mint-searchbar-inner .mintui-search {\n    font-size: 18px;\n    color: #9e9e9e;\n    margin: 0 8px;\n}\n.contacts .list {\n    overflow-y: scroll;\n    -webkit-overflow-scrolling: touch;\n}\n.contacts .list ul li {\n    height: 75px;\n    padding: 0px;\n    border-bottom: 1px solid #e2e2e2;\n    display: flex;\n    align-items: center;\n}\n.contacts .list ul li div, .contacts .list ul li a {\n    display: flex;\n    align-items: center;\n    width: 100%;\n}\n.contacts .list ul li > span {\n    margin-right: 10px;\n}\n.contacts .list ul li input, .contacts .list ul li img, .contacts .list ul li span {\n    margin-left: 15px;\n}\n.contacts .total {\n    text-align: center;\n    font-size: 14px;\n    margin: 0 auto;\n    padding: 12px;\n}\n.contacts .selected {\n    position: fixed;\n    left: 0;\n    bottom: 0;\n    height: 75px;\n    width: 100%;\n    display: flex;\n    flex-direction: row;\n    justify-content: flex-start;\n    align-items: center;\n    background-color: #f3f3f3;\n}\n.contacts .selected ul {\n    display: flex;\n    width: 77%;\n    overflow: auto;\n    margin-left: 5px;\n}\n.contacts .selected ul li {\n    height: 50px;\n    width: 50px;\n    margin-left: 5px;\n}\n.contacts .selected span {\n    color: #4e8dff;\n    text-align: center;\n    margin: 0 auto;\n}\n.contacts .list::-webkit-scrollbar {\n    width: 0px;\n    height: 0px;\n}\n.contacts .selected ul::-webkit-scrollbar {\n    width: 0px;\n    height: 0px;\n}\n"],"sourceRoot":""}]);

